<template>
    <div>
        <div :id='model.id' @click="toggle" @dblclick="changeType" draggable='true' @dragstart='dragStart' @dragend='dragEnd' @dragover='dragOver' @dragenter='dragEnter' @dragleave='dragLeave' @drop='drop' class='treeNodeText' :style='styleObj'>
            <span :class="[isClicked ? 'nodeClicked' : '','vue-drag-node-icon']"></span>
            {{layer[model.id].name}}
            <span @click="removeChild(model.id)" v-if='model.id !="1"'>&nbsp;x</span>
        </div>
        <div class='treeMargin' v-show="open" v-if="isFolder">
            <item v-for="model in model.children" :model="model" :layer='layer' :key='model.id'>
            </item>
            <div class='changeTree' @click="addChild" @drop='dropPlus' @dragover='dragOverPlus' @dragenter='dragEnterPlus'>+</div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

let count = 1000
let id = 1000
let fromData = ''
let toData = ''
let fromParentModelChildren = ''  // from 节点的父组件的model

export default {
    name: 'VueDragTreeCom',
    data: function () {
        return {
            open: false,
            styleObj: {
                background: 'white'
            },
            isClicked: false,
        }
    },
    props: {
        model: Object,
        layer: Object,
    },
    computed: mapState({
        isFolder() {
            return this.model.children &&
                this.model.children.length
        },
        curNodeClicked: state => state.curNodeClicked,
    }),
    methods: {
        ...mapMutations([
            'updateCurNodeClicked',
        ]),
        toggle() {
            if (this.isFolder) {
                this.open = !this.open
            }
            this.updateCurNodeClicked(this.model.id)

            // 纪录节点被点击的状态
            this.isClicked = !this.isClicked
        },
        exchangeData(rootCom, from, to) {
            //如果drag的目的地是 + - 符号的话，退出。
            if (!to || from.id == to.id || typeof to == 'string') {
                return
            }
            from = JSON.parse(JSON.stringify(from))
            to = JSON.parse(JSON.stringify(to))
            // copy一个,最后再赋值给state.treeData.这样能保证值的变化都会触发视图刷新(因为JS判断引用类型是否相同是根据内存地址.)
            let treeData = JSON.parse(JSON.stringify(this.model))
            let nodeStack = [treeData]
            let status = 0
            // 如果from或者to节点存在父子/祖辈关系，会报id of undefined的错。这种情况不考虑拖拽功能，所以catch住，返回/return就行
            try {
                // 广度优先遍历,找到涉及数据交换的两个对象.然后交换数据.
                while (!(status === 2)) {
                    let item = nodeStack.shift()
                    if (item.id == from.id) {
                        item.id = to.id
                        if (to.children && to.children.length > 0) {
                            item['children'] = to.children
                        } else {
                            item.children = []
                        }
                        status++
                        //找到后,跳出当前循环.
                        continue;
                    }
                    if (item.id == to.id) {
                        item.id = from.id
                        if (from.children && from.children.length > 0) {
                            item['children'] = from.children
                        } else {
                            item.children = []
                        }
                        status++
                        //找到后,跳出当前循环.
                        continue;
                    }
                    if (item.children && item.children.length > 0) {
                        nodeStack = nodeStack.concat(item.children)
                    }
                }
            } catch (e) {
                return
            }
            rootCom.assignData(treeData)
        },
        changeType() {
            if (!this.isFolder) {
                this.$set(this.model, 'children', [])
                this.addChild()
                this.open = true
            }

            this.isClicked = true
        },
        findRoot() {
            let ok = false
            let that = this
            while (!ok) {
                // 如果父组件有data属性，说明当前组件是Tree组件递归调用发生时的第一个组件。
                // Warning: 因为是判断data属性是否存在，所有在别人使用该组件时，属性名必须得是data
                if (that.$parent.data) {
                    ok = true
                    // 交换两者的数据 
                    // that vue-drag-tree组件（顶级）
                    // that.exchangeData(that.$parent, from, to)
                    break
                }
                that = that.$parent
            }
            return that
        },
        addChild() {
            // 分别向layer,model里增加数据。
            const temp_id = id++
            this.model.children.push({
                id: temp_id
            })
            this.$set(this.layer, temp_id, {
                name: '节点' + count++,
                styles: {
                    json_data: '',
                    css: '',
                },
                text: {
                    plain_text: `测试文本${count - 1}`,
                },
            })
        },
        removeChild(id) {
            // 获取父组件的model.children
            let parent_model_children = this.$parent.model.children

            // 在父组件model.children里删除
            for (let index in parent_model_children) {
                // 找到该删的id
                if (parent_model_children[index].id == id) {
                    parent_model_children = parent_model_children.splice(index, 1)
                    break
                }
            }

            // 连同该节点的子节点，一同从layer删除
            let nodeStack = [this.model]
            while (nodeStack.length != 0) {
                let item = nodeStack.shift()
                delete this.layer[item.id]
                if (item.children && item.children.length > 0) {
                    nodeStack = nodeStack.concat(item.children)
                }
            }
        },
        dragStart(e) {
            // fromData = this.model
            e.dataTransfer.effectAllowed = "move";
            e.dataTransfer.setData("nottext", e.target.innerHTML);
        },
        dragEnd(e) {
            fromData = undefined
            toData = undefined
            fromParentModelChildren = undefined
            // e.target.style.background = '#7B1FA2'
        },
        dragOver(e) {
            e.preventDefault()
        },
        dragOverPlus(e) {
            e.preventDefault()
        },
        dragEnterPlus(e) {
        },
        dragEnter(e) {
            toData = this.model
            let rootTree = this.findRoot()
            rootTree.exchangeData(rootTree.$parent, fromData, toData)
        },
        dragLeave(e) {
            fromData = this.model
            fromParentModelChildren = this.$parent.model.children
        },
        drop(e) {
            // console.log('drop')
            // toData = this.model
            // e.target.style.background = '#7B1FA2'
        },
        dropPlus(e) {
            // 把from节点插入到当前层级节点的最后一个
            if (this.model.hasOwnProperty('children')) {
                this.model.children.push(fromData)
            } else {
                this.model.children = [fromData]
            }

            console.log('fromParentModelChildren', fromParentModelChildren)
            // 把from节点从之前的节点删除
            for (let i in fromParentModelChildren) {
                console.log(PageTransitionEvent[i], 'iii')
                if (fromParentModelChildren[i].id == fromData.id) {
                    fromParentModelChildren.splice(i, 1)
                }
            }
        },
    },
    watch: {
        curNodeClicked(val, oldVal) {
            // 节点的高亮显示
            // 当curNodeClicked变化时，说明用户点击了不同的节点
            // 然后从vue-drag-tree的顶级组件（组件递归中第一个组件）开始重置每个组件的styleObj

            if (val == this.model.id) {
                let ok = false
                let that = this
                while (!ok) {
                    if (that.$parent.data) {
                        ok = true
                        that = that.$parent
                        break
                    }
                    that = that.$parent
                }
                let nodeStack = [that.$children[0]]
                while (nodeStack.length != 0) {
                    let item = nodeStack.shift()
                    item.styleObj.background = 'white'
                    if (item.$children && item.$children.length > 0) {
                        nodeStack = nodeStack.concat(item.$children)
                    }
                }
                this.styleObj.background = '#D3DCE6'
            }
        }
    },
    beforeCreate() {
        this.$options.components.item = require('./vue-drag-tree')
    },
}
</script>

<style>
.item {
    cursor: pointer;
}

.bold {
    font-weight: bold;
}

.treeNodeText {
    margin: 2px;
    padding: 0.2rem 0.5rem;
    width: fit-content;
    background: #F9FAFC;
    font-size: 18px;
    color: #324057;
}

.treeMargin {
    margin-left: 2rem;
}

.changeTree {
    width: 1rem;
}

.vue-drag-node-icon {
    display: inline-block;
    width: 0;
    height: 0;
    border-left: 6px solid black;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-right: 0 solid yellow;
    transition: transform .3s ease-in-out;
}

.nodeClicked {
    transform: rotate(90deg);
}
</style>


