<template>
    <div class='previewContainer'>
        <el-card>
            <div class='pvContent'>
                {{previewContent}}
            </div>
        </el-card>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'StylePreviewName',
    computed: mapState([
        'previewContent',
    ]),
}
</script>

<style scoped>
.previewContainer{
    width: 20%;
}
.previewContainer .pvContent {
    min-height: 21rem;
    width: 100%;
    padding: 1rem;
}
</style>



