<template>
    <div class='webPreviewContainer'>
        <el-card>
            <div class='pvContent' v-html='html'>
            </div>
        </el-card>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'WebPreviewName',
    data() {
        return {
            html: '',
        }
    },
    computed: mapState([
        'startWebPreview',
        'data_layer',
    ]),
    methods: {
    },
    watch: {
        startWebPreview(val) {
            // 生成HTML并插入到页面中
            this.html = ''
            console.log('开始预览')
            for (let i in this.data_layer) {
                if (this.data_layer[i].text.plain_text) {
                    this.html += `<div class='class${i}'>${this.data_layer[i].text.plain_text}</div>`
                }
            }
        }
    }
}
</script>

<style scoped>
.webPreviewContainer {
    min-width: 15%;
}

.webPreviewContainer .pvContent {
    min-height: 21rem;
    width: 100%;
    padding: 1rem;
}
</style>



