<template>
    <div class='domTreeContainer'>
        <div class='layers'>
            <!--<item :model='treeData'></item>-->
            <vue-drag-tree :model='data' :layer='data_layer'></vue-drag-tree>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import VueDragTree from './partials/vue-drag-tree'

// 新生成的节点的所用的id,此处id会递增.
// Warning:  id不能被声明为let,否则在子孙节点中，id有重复现象
var id = 1000
export default {
    name: 'DomTreeName',
    computed: mapState({
        // data: state => state.treeData,
        data: state => state.data_layout,
        data_layer: state => state.data_layer,
    }),
    methods: {
        ...mapMutations([
            'updateCurNodeClicked',
            'updateTreeData',
        ]),
        assignData(data) {
            this.updateTreeData(data)
        },
    },
    components: {
        'vue-drag-tree': VueDragTree,
    }
}
</script>

<style scoped>
.domTreeContainer {
    min-width: 15%;
}

.domTreeContainer .layers {
    min-height: 25rem;
}
</style>



