<template>
    <div class='styleEdit'>
        <el-input v-model="input" class='inputNameClass' placeholder="请输入名称"></el-input>
        <textarea v-model='textarea' placeholder='首行如果有回车符号则会有问题...'>
        </textarea>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'styleEditName',
    data() {
        return {
            textarea: '',
            input: '',
        }
    },
    computed: mapState([
        'editStyle_clicked',
        'styles',
        'styleEdit_textarea',
        'styleEdit_input',
    ]),
    methods: {
        ...mapMutations([
            'updateStyleData',
            'updateStyleName',
            'updateStyleEdit_textarea',
            'updateStyleEdit_input',
        ]),
        jsonToCss(json) {
            return json.replace(/"/g, '').replace(/,/g, ';')
        },
        cssToJson(css) {
            let json = css
            let key = json.match(/([\S.]*?):/g)
            key = key.map((ele) => ele.replace(':', ''))
            for (let item of key) {
                json = json.replace(item, `"${item}"`)
            }
            let value = json.match(/:(.*?);/g)
            value = value.map((ele) => ele.replace(/^[ :]|[ ;]$/g, ''))
            for (let item of value) {
                json = json.replace(item, `"${item}"`)
            }
            return json.replace(/;/g, ',')
        }
    },
    watch: {
        editStyle_clicked(val) {
            if (this.styles.hasOwnProperty(this.editStyle_clicked)) {
                let styleData = this.styles[this.editStyle_clicked].data
                console.log('styleData:  ', styleData)
                // 如果有内容,即CSS
                if (styleData !== '') {
                    // this.textarea = this.jsonToCss(styleData)
                    this.textarea = styleData
                } else {
                    this.textarea = ''
                }
                this.input = this.styles[this.editStyle_clicked].name

            } else {
                // 此时this.editStyle_clicked 是-1，表示用户连续两次点击同一标签。
                // 这时textarea,input设置为空.
                this.textarea = ''
                this.input = ''
            }
        },
        textarea(val) {
            // 如果用户输入时，对应的style标签有被点击。
            console.log('textarea changed: ', val)
            if (val) {
                if (!(this.editStyle_clicked == '' || this.editStyle_clicked == '-1')) {
                    let jsonStr
                    try {
                        // jsonStr = this.cssToJson(val)
                        jsonStr = val
                    } catch (e) {
                        // 因为是实时监听．所以this.cssToJson里的map,replace等函数就会报错TypeError．忽略掉就ok
                    }
                    finally {
                        this.updateStyleData({ key: this.editStyle_clicked, value: jsonStr })
                    }
                }
            }
        },
        input(val) {
            // 如果用户输入时，对应的style标签有被点击。
            if (!(this.editStyle_clicked == '' || this.editStyle_clicked == '-1')) {
                this.updateStyleName({ key: this.editStyle_clicked, name: val })
            }
        },
    },
    created() {
        if (this.editStyle_clicked) {
            // 根据state里的editStyle_clicked初试化textarea和input的内容
            this.textarea = this.styles[this.editStyle_clicked].data
            this.input = this.styles[this.editStyle_clicked].name
        }
    },
}
</script>

<style scoped>
.styleEdit {
    width: 100%;
    min-height: 25rem;
}

.styleEdit textarea {
    width: 100%;
    padding: 1rem;
    min-height: 21.2rem;
    font-size: 20px;
}
</style>




