import box_position from '../assets/js/source-js/style/functions/box_position.js'
import box_layout from '../assets/js/source-js/style/functions/box_layout.js'
const insertStyle = require('insert-styles')


export default {
    updateStyle(state, {
        id,
        key
    }) {
        let treeData = state.treeData

        // 遍历domTree搜寻对应特定节点name
        // 先把第一级节点push进数组。
        let node = [treeData]
        let ok = false
        while (!ok) {
            let item = node.shift()
            if (item.id == id) {
                item.styles = [key]
                ok = true
            } else if (item.children && item.children.length > 0) {
                node = node.concat(item.children)
            }
        }
    },
    updateCurNodeClicked(state, id) {
        state.curNodeClicked = String(id)
    },
    updateNodeText(state, {
        id,
        text
    }) {
        state.data_layer[id].text.plain_text = text
    },
    updateNodeName(state, {
        id,
        name
    }) {
        state.data_layer[id].name = name
    },
    changeEditStyle_clicked(state, id) {
        state.editStyle_clicked = id
    },
    changeLayoutStyle_clicked(state, id) {
        state.layoutStyle_clicked = id
    },
    updateCSS(state, css) {
        state.css = css
    },
    updateStyleEdit_textarea(state, text) {
        state.styleEdit_textarea = text
    },
    updateStyleEdit_input(state, text) {
        state.styleEdit_input = text
    },
    updatePreviewContent(state, id) {
        // 如果存在属性[id] 并且该id的value不为空。 更新previewContent
        if (state.data_layer.hasOwnProperty(id)) {
            let styleObj = state.data_layer[id].style.json_data
            console.log('node styleObj-->', styleObj)

            // 节点唯一的class name
            let classId = 'class' + id

            let result_layout = ''
            if (styleObj.hasOwnProperty('layout')) {
                let layout = styleObj.layout

                result_layout = box_layout({
                    selector: classId,
                    layout_rule: layout.layout_rule,
                    mainaxis_direction: layout.mainaxis_direction,
                    mainaxis_overflow_hide: layout.mainaxis_overflow_hide,
                    mainaxis_items_align: layout.mainaxis_items_align,
                    mainaxis_gutter: layout.mainaxis_gutter,
                    mainaxis_align: layout.mainaxis_align,
                    mainaxis_wrap: layout.mainaxis_wrap,
                    crossaxis_reverse: layout.crossaxis_reverse,
                    crossaxis_overflow_hide: layout.crossaxis_overflow_hide,
                    crossaxis_items_align: layout.crossaxis_items_align,
                    crossaxis_gutter: layout.crossaxis_gutter,
                    grids_cols: layout.grids_cols,
                    size_fit_x: layout.size_fit_x,
                    size_fit_y: layout.size_fit_y,
                })
            }
            console.log('result_layout-->', result_layout)

            let result_position = ''
            if (styleObj.hasOwnProperty('position')) {
                let position = styleObj.position
                let rule = box_position.set_position_rule(position.rule)

                if (position.rule == 'absolute') {
                    result_position += 'position: absolute;'
                    let x_result = box_position.position_absolute_x({
                        selector: classId,
                        absolute_x_align_center: position.absolute_x_align_center,
                        absolute_x_align_start: position.absolute_x_align_start,
                        absolute_x_align_start_space: position.absolute_x_align_start_space,
                        absolute_x_align_end: position.absolute_x_align_end,
                        absolute_x_align_end_space: position.absolute_x_align_end_space,
                        size_x: position.size_x,
                        size_x_max: position.size_x_max,
                        size_x_min: position.size_x_min,
                    })
                    let y_result = box_position.position_absolute_y({
                        selector: classId,
                        absolute_y_align_center: position.absolute_y_align_center,
                        absolute_y_align_start: position.absolute_y_align_start,
                        absolute_y_align_start_space: position.absolute_y_align_start_space,
                        absolute_y_align_end: position.absolute_y_align_end,
                        absolute_y_align_end_space: position.absolute_y_align_end_space,
                        size_y: position.size_y,
                        size_y_max: position.size_y_max,
                        size_y_min: position.size_y_min,
                    })

                    result_position += (x_result['.' + classId] + y_result['.' + classId])
                } else if (position.rule == 'relative') {
                    // relative
                    result_position += 'position: relative;'

                    let relative_result = box_position.position_relative({
                        selector: classId,
                        relative_grow: position.relative_grow,
                        relative_shrink: position.relative_shrink,
                        relative_align: position.relative_align
                    })
                    result_position += relative_result['.' + classId]
                } else {
                    console.log('position.rul == auto 时')
                }
            }

            console.log('result_position-->', result_position)

            let result_bg = ''
            if (styleObj.hasOwnProperty('bg')) {
                let bg = styleObj.bg
                if (bg.flag) {
                    result_bg = `background: auto-${bg.auto};`
                } else {
                    if (bg.manual.name === 'solid') {
                        result_bg = `background: ${bg.manual.value};`
                    } else if (bg.manual.name === 'gradient') {
                        result_bg = `background: linear-gradient(${bg.manual.value});`
                    } else {
                        result_bg = `background: ${bg.manual.value};`
                    }
                }
            }
            // console.log('bg result>>', result_bg)

            // let result_border = ''
            // if (styleObj.hasOwnProperty('border')) {
            //     let border = styleObj.border
            //     if (border['is-separate']) {
            //         result_border = `border: ${border.width}% ${border.corlor}% ${border.corner3}% ${border.corner4}%;`
            //     } else {
            //         result_border = `border: ${border.corner1}%;`
            //     }
            // } console.log('border result>>', result_border)

            let result_shadows = ''
            if (styleObj.hasOwnProperty('shadows')) {
                let shadows = styleObj.shadows
                if (shadows.is_inset) {
                    result_shadows = `box-shadow: ${shadows.x} ${shadows.y} ${shadows.blur} ${shadows.spread} ${shadows.color} inset;`
                } else {
                    result_shadows = `box-shadow: ${shadows.x} ${shadows.y} ${shadows.blur} ${shadows.spread} ${shadows.color};`
                }
            }
            // console.log('shadows result>>', result_shadows)

            let css = ''

            // 先不管.[classid]对应的样式
            for (let i in result_layout) {
                if (i !== '.' + classId) {
                    css += `
                    ${i}{
                        ${result_layout[i]}
                    }
                    `
                }
            }

            // 如果存在result_layout中存在.[classId]属性的话把之同其他样式(position...)一同填加到css中
            if (result_layout.hasOwnProperty['.' + classId]) {
                if (result_position) {
                    css += `
                        .${classId}{
                            ${result_layout[i]}
                            ${result_position}
                        }
                    `
                }
            } else {
                // 单独填加其他样式到css中
                if (result_position) {
                    css += `
                        .${classId}{
                            ${result_position}
                        }
                    `
                }
            }

            state.previewContent = css
            insertStyle(css)

            //同时把css存储到JSON里
            state.data_layer[id].style.css = css
        } else {
            state.previewContent = ''
        }
    },
    transformData(state, {
        from,
        to
    }) {
        // 交换from,to两个节点的数据

        from = JSON.parse(JSON.stringify(from))
        to = JSON.parse(JSON.stringify(to))

        // copy一个,最后再赋值给state.treeData.这样能保证值的变化都会触发视图刷新(因为JS判断引用类型是否相同是根据内存地址.)
        let treeData = JSON.parse(JSON.stringify(state.treeData))
        let nodeStack = [treeData]
        let status = 0

        // 广度优先遍历,找到涉及数据交换的两个对象.然后交换数据.
        while (!(status === 2)) {
            let item = nodeStack.shift()
            if (item.id == from.id) {
                item.id = to.id
                item.name = to.name
                if (to.children && to.children.length > 0) {
                    item['children'] = to.children
                } else {
                    item.children = []
                }
                status++
                //找到后,跳出当前循环.
                continue;
            }
            if (item.id == to.id) {
                item.id = from.id
                item.name = from.name
                if (from.children && from.children.length > 0) {
                    item['children'] = from.children
                } else {
                    item.children = []
                }
                status++
                //找到后,跳出当前循环.
                continue;
            }
            if (item.children && item.children.length > 0) {
                nodeStack = nodeStack.concat(item.children)
            }
        }
        state.treeData = treeData
    },
    addStyleJSON(state, {
        id,
        json
    }) {
        // state.styleJSON[id] = JSON.parse(JSON.stringify(json))
        state.data_layer[id].style.json_data = JSON.parse(JSON.stringify(json))
    },
    addStartWebPreview(state) {
        state.startWebPreview++
    },
    updateTreeData(state, data) {
        state.data_layout = data
    }
}


