<template>
    <el-card class='jsonContainer'>
        <pre>{{data_layout}}</pre>
    </el-card>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name: 'JSONCom',
    computed: mapState({
        data_layout: state => JSON.stringify(state.data_layout, null, 2)
    }),
}
</script>

<style scoped>
.jsonContainer {
    min-width: 20%;
}
</style>



