    computed: mapState({
        curNodeClicked: state => state.curNodeClicked,
        startWebPreview: state => state.startWebPreview,
    }),
    methods: {
        ...mapMutations([
            'updateNodeText',
            'updateNodeName',
            'updatePreviewContent',
            'addStyleJSON',
            'addStyleText',
            'addStartWebPreview',
        ]),
        insertForm() {
            let that = this
            $('form').jsonForm({
                "schema": this.schema,
                "form": this.form,
                "onSubmit": function (errors, values) {
                    if (errors) {
                        $('#res').html('<p>表单数据有错</p>');
                        console.error('onSubmit:', errors)
                    }
                    else {
                        // 这里不能执行函数，否则会强制刷新页面（应该是submit按钮引起的，但相关代码在jsonform里，在不改变源代码的情况下：只能把values赋值给其他变量，然后监听这个变量，进而更新stylejson
                        that.values = values
                    }
                }
            });
        },
    },
    watch: {
        values(val) {
            // 更新当前节点的name和text信息
            console.log('css_json_val', val,val.variable)
            // 如果存在且不为空，更新节点name,text
            if (val.variable) {
                console.log('this__')
                if (val.variable.hasOwnProperty('name') && val.variable.name !== '') {
                    this.updateNodeName({ id: this.curNodeClicked, name: val.variable.name })
                }
                if (val.variable.hasOwnProperty('text') && val.variable.text !== '') {
                    this.updateNodeText({ id: this.curNodeClicked, text: val.variable.text })
                }
            }


            // 更新节点的styleJSON数据。
            this.addStyleJSON({ id: this.curNodeClicked, json: val })

            // 同时因为该节点对应的styleJSON变化了，更新previewContent(style)的内容
            this.updatePreviewContent(this.curNodeClicked)

            // 自增startPreview，提醒该实时进行Web预览了
            this.addStartWebPreview()
        }
    },
    mounted() {
        this.insertForm()
    },
