let length_json = v => {
    if (v.match(/^\d+\.?\d*$/)) { //纯数字
        return v;
    } else if (v.match(/^(\d+\.?\d*)%$/)) {　 // 百分数
        return v;
    } else if (v.match(/^=\s*(.*)$/)) {　 // = 数字
        return v.match(/^=\s*(.*)$/)[1];
    }
    return '0';
}

let length_css = v => {
    if (v.match(/^\d+\.?\d*$/)) {
        return eval(v / 16) + 'rem';
    } else if (v.match(/^(\d+\.?\d*)%$/)) {
        return v;
    } else {
        return 'calc(' + v + ')';
    }
}

export default {
    length_json: length_json,
    length_css: length_css,
}


