import lengthObj from './length'

let overflow = v => {
    switch (v) {
        case 'show':
            return `
                overflow: visible;
            `
        case 'hide':
            return `
                overflow: hidden;
            `
        case 'scroll':
            return `
                overflow: auto;
            `
    }
}

let overflow_x = v => {
    switch (v) {
        case 'show':
            return `
                overflow-x: visible;
            `
        case 'hide':
            return `
                overflow-x: hidden;
            `
        case 'scroll':
            return `
                overflow-x: auto;
            `
    }
}

let overflow_y = v => {
    switch (v) {
        case 'show':
            return `
                overflow-y: visible;
            `
        case 'hide':
            return `
                overflow-y: hidden;
            `
        case 'scroll':
            return `
                overflow-y: auto;
            `
    }
}

let main_axis_direction = (dir = x) => {
    switch (dir) {
        case 'x':
            return `
                flex-direction: row;
            `
        case 'y':
            return `
                flex-direction: column;
            `
        case 'x':
            return `
                flex-direction: row-reverse;
            `
        case 'x':
            return `
                flex-direction: cloumn-reverse;
            `
    }
}

let set_mainaxis_direction_value = v => {
    switch (v) {
        case 'x':
            return `row;`
        case 'y':
            return `flex-end;`
        case 'xr':
            return `center;`
        case 'yr':
            return `space-between;`
    }
}

let set_mainaxis_align_value = v => {
    switch (v) {
        case 'start':
            return `flex-start;`
        case 'end':
            return `flex-end;`
        case 'center':
            return `center;`
        case 'between':
            return `space-between;`
        case 'around':
            return `space-around;`
        case 'stretch':
            return `stretch;`
    }
}

// selector 指节点的唯一class.如： container
let box_layout = ({
    selector,
    layout_rule = 'off',
    mainaxis_direction = 'x',
    mainaxis_overflow_hide = false,
    mainaxis_items_align = 'start',
    mainaxis_gutter = 0,
    mainaxis_align = 'start',
    mainaxis_wrap = false,
    crossaxis_reverse = false,
    crossaxis_overflow_hide = false,
    crossaxis_items_align = 'start',
    crossaxis_gutter = 'mainaxis_gutter',
    grids_cols = 1,
    size_fit_x = false,
    size_fit_y = false
} = {}) => {
    if (layout_rule == 'off') {
        let results = {
            ['.' + selector + '>*']: 'position: absolute;',
        }
        return results
    } else {
        let results = {
                ['.' + selector]: '',
                ['.' + selector + '>*']: '',
                ['.' + selector + '>*:not(:first-child)']: '',
                ['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)']: '',
                ['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)' + ':nth-last-child(' + grids_cols + '-n)']: '',
                ['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)' + ':nth-last-child(' + grids_cols + '-n)~*']: '',
            } // 根据不同CSS选择器存储对应CSS结果.key:选择器，value:对应CSS

        // result 存储results['.' + selector]的value。最后赋值给它
        results['.' + selector + '>*'] += `
            position: relative;
        `

        results['.' + selector] += `
            display: inline-flex;
            align-content: ${set_mainaxis_align_value(mainaxis_align)}
        `

        if (mainaxis_wrap) {
            if (mainaxis_direction == 'x' || mainaxis_direction == 'xr') {
                if (size_fit_y == false && crossaxis_overflow_hide) {
                    results['.' + selector] += `overflow-y: hide;`
                }
            } else if (mainaxis_direction == 'y' || mainaxis_direction == 'yr') {
                if (size_fit_x == false && crossaxis_overflow_hide) {
                    results['.' + selector] += `overflow-x: hide;`
                }
            }
        } else {
            if (mainaxis_direction == 'x' || mainaxis_direction == 'xr') {
                if (size_fit_x == false && mainaxis_overflow_hide) {
                    results['.' + selector] += `overflow-x: hide;`
                }
            } else if (mainaxis_direction == y || mainaxis_direction == yr) {
                if (size_fit_y == false && mainaxis_overflow_hide) {
                    results['.' + selector] += `overflow-y: hide;`
                }
            }
        }

        if (layout_rule == 'custom') {
            results['.' + selector] += `${main_axis_direction(mainaxis_direction)}`
            if (mainaxis_wrap) {
                if (crossaxis_reverse) {
                    results['.' + selector] += `flex-wrap: wrap-reverse;`
                } else {
                    results['.' + selector] += `flex-wrap: wrap;`
                }
            } else {
                results['.' + selector] += `flex-wrap: nowrap;`
            }
        } else if (layout_rule == 'stack') {
            results['.' + selector] += `
                ${main_axis_direction(mainaxis_direction)}
			    flex-wrap: nowrap;
            `
            if (mainaxis_direction == 'x') {
                results['.' + selector + '>*:not(:first-child)'] += `margin-left: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
            } else if (mainaxis_direction == 'y') {
                results['.' + selector + '>*:not(:first-child)'] += `margin-top: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
            } else if (mainaxis_direction == 'xr') {
                results['.' + selector + '>*:not(:first-child)'] += `margin-right: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
            } else if (mainaxis_direction == 'yr') {
                results['.' + selector + '>*:not(:first-child)'] += `margin-bottom: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
            }
        } else if (layout_rule == 'flow') {
            results['.' + selector] += `${main_axis_direction(mainaxis_direction)}`
            if (crossaxis_reverse) {
                results['.' + selector] += `flex-wrap: wrap-reverse;`
            } else {
                results['.' + selector] += `flex-wrap: wrap;`
            }

            if (mainaxis_direction == 'x') {
                results['.' + selector + '>*'] += `margin-right: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
                if (crossaxis_reverse) {
                    results['.' + selector + '>*'] += `margin-top: ${crossaxis_gutter};`
                } else {
                    results['.' + selector + '>*'] += `margin-bottom: ${crossaxis_gutter};`
                }
            } else if (mainaxis_direction == 'xr') {
                results['.' + selector + '>*'] += `margin-left: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
                if (crossaxis_reverse) {
                    results['.' + selector + '>*'] += `margin-top: ${crossaxis_gutter};`
                } else {
                    results['.' + selector + '>*'] += `margin-bottom: ${crossaxis_gutter};`
                }
            } else if (mainaxis_direction == 'y') {
                results['.' + selector + '>*'] += `margin-bottom: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
                if (crossaxis_reverse) {
                    results['.' + selector + '>*'] += `margin-left: ${crossaxis_gutter};`
                } else {
                    results['.' + selector + '>*'] += `margin-right: ${crossaxis_gutter};`
                }
            } else if (mainaxis_direction == 'yr') {
                results['.' + selector + '>*'] += `margin-top: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
                if (crossaxis_reverse) {
                    results['.' + selector + '>*'] += `margin-left: ${crossaxis_gutter};`
                } else {
                    results['.' + selector + '>*'] += `margin-right: ${crossaxis_gutter};`
                }
            }
        } else if (layout_rule == 'grids') {
            results['.' + selector] += `
                ${main_axis_direction('x')}
                flex-wrap: wrap;
            `

            let gaps = grids_cols - 1
            let gaptotal = gaps * mainaxis_gutter

            if (mainaxis_gutter == 0) {
                results['.' + selector + '>*'] += `width: ${1/grids_cols}rem;`
            } else {
                results['.' + selector + '>*'] += `width: ${1 - grids_cols/grids_cols}rem;`
                results['.' + selector + '>*'] += `margin-left: ${lengthObj.length_css(lengthObj.length_json(String(mainaxis_gutter)))};`
            }
            if (crossaxis_gutter != 0) {
                results['.' + selector + '>*'] += `margin-bottom: ${crossaxis_gutter};`
            }

            results['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)'] += `margin-left: 0;`
            results['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)' + ':nth-last-child(' + grids_cols + '-n)'] += `margin-bottom: 0;`
            results['.' + selector + '>*:nth-child(' + grids_cols + 'n+1)' + ':nth-last-child(' + grids_cols + '-n)~*'] += `margin-bottom: 0;`
        }

        //循环，把每个value用{}包起来
        for (let i of Object.keys(results)) {
            if (results[i] !== '') {
                // results[i] = `{${results[i]}}`
            } else {
                // 如果key没有value，则删除
                delete results[i]
            }
        }
        console.log('layout.js --> result -->', results)
        return results
    }
}

export default box_layout


