<template>
    <div class='editStyleContainer'>
        <StyleSection></StyleSection>
        <StyleEdit ref='styleedit'></StyleEdit>
    </div>
</template>

<script>
import StyleEdit from '@/components/styleCommon/StyleEdit'
import StyleSection from '@/components/styleCommon/StyleSection'
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'EditStyleCom',
    components: {
        StyleEdit: StyleEdit,
        StyleSection: StyleSection,
    },
    computed: mapState([
        'editStyle_clicked',
        'style',
    ]),
    created() {
        // 点击Styles时,根据state里的editStyle_clicked初试化StyleEdit组件的 textarea 和 input 属性
        // 如果值不为空(因为最开始时那一次该值为空.)
        console.log('这里:::::::')
        if (this.editStyle_clicked) {
            console.log(this.$refs)
            let styleEditCom = this.$refs.styleedit
            console.log(styleEditCom)
            // 如果改组件引用不是undefined.因为会触发本created()两次,第一次时为undefined
            if (styleEditCom) {
                console.log('data1', this.styles[this.editStyle_clicked].data)
                styleEditCom.textarea = this.styles[this.editStyle_clicked].data
                styleEditCom.input = this.styles[this.editStyle_clicked].name
            }
        }
    }
}
</script>

<style scoped>
.editStyleContainer {
    display: flex;
}

.editStyleContainer>div:first-child {
    width: 30%;
}
</style>



