<template>
    <div class='commonHeader'>
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <router-link :to='{name:"Main"}'>
                <el-menu-item index='1'>
                    Layout
                </el-menu-item>
            </router-link>
            <router-link :to='{name:"EditStyle"}'>
                <el-menu-item index='2'>
                    Style
                </el-menu-item>
            </router-link>
            <el-button class='generateBtn' type='text' @click='showHC'>生成HTML/CSS</el-button>
        </el-menu>
    
        <el-dialog title="HTML/CSS" :visible.sync="dialogVisible" size="large">
            <!--<span>这是一段信息</span>-->
            <div class='dialogContainer'>
                <div class='showHTML'>
                    {{html_info}}
                </div>
                <div class='showCSS'>
                    {{css_info}}
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
// import generateObj from '@/assets/js/generateHC'
// import generateBento from '@/assets/js/generateBento'

// 第三方库,把Styles文本整体插入到head中.
// const insertStyles = require('insert-styles')

export default {
    name: 'HeaderName',
    data() {
        return {
            activeIndex: '1',
            dialogVisible: false,
            html_info: '',
            css_info: '',
        }
    },
    computed: mapState([
        'domTree',
        'styles',
        'textTree',
        'editStyle_clicked',
        'styleEdit_textarea',
        'styleEdit_input',
        'bento',
    ]),
    methods: {
        ...mapMutations([
            'updateCSS',
            'updateStyleEdit_textarea',
            'updateStyleEdit_input',
            'updateBento',
        ]),
        handleSelect(key) {
            // if (key === '1') {
            //     // 更新state的css
            //     this.updateCSS(this.generateCSS())
            //     // 把生成的css插入到页面的head里
            //     insertStyles(this.generateCSS())
            // } else if (key === '2') {

            // }
        },
        showHC() {
            this.generateHC();
            this.updateBento(generateBento(this.domTree, this.textTree, this.styles))
            this.dialogVisible = true
        },
        generateHC() {
            this.generateCSS()
            this.generateHTML()
        },
        generateHTML() {
            let gene = node => {
                const div = `<div class='${node.class}'>${node.text}`
                let sec = ''
                if (node.childNodes && node.childNodes.length > 0) {
                    sec = node.childNodes.map((item) => {
                        return gene(item)
                    })
                }
                return div + sec.toString().replace(/,/g, '') + '</div>'
            }

            this.html_info = gene(this.bento[0])
        },
        generateCSS() {
            this.css_info = generateObj.generateCSS(this.domTree, this.textTree, this.styles)
            return this.css_info
        },
    },
    created() {
        // 如果有样式信息的话,插入到页面中.
        // this.updateCSS(this.generateCSS())
        // insertStyles(this.generateCSS())
    }
}
</script>

<style scoped>
.generateBtn {
    position: absolute;
    right: 1rem;
    top: 0.7rem;
}

.dialogContainer {
    display: flex;
    font-size: 20px;
    padding: 1rem;
    min-height: 20rem;
}

.showHTML {
    padding: 1rem;
    background: #324057;
    margin-right: 2rem;
    width: 60%;
    color: white;
}

.showCSS {
    padding: 1rem;
    width: 40%;
    color: black;
    background: #F9FAFC;
}
</style>



