import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import EditStyle from '@/components/EditStyle'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Main',
        components: {
            rootView: Main,
        }
    }, {
        path: '/editstyle',
        name: 'EditStyle',
        components: {
            rootView: EditStyle,
        }
    }],
})


