import lengthObj from './length'

let set_position_rule = ({
    selector,
    rule = 'auto'
} = {}) => {
    let results = {
        ['.' + selector]: '',
    }
    rule == 'absolute' ? results['.' + selector] = 'position: abslute;' : results['.' + selector] = 'position: relative;';

    return results
}

let position_absolute_x = ({
    selector,
    absolute_x_align_center = false,
    absolute_x_align_start = false,
    absolute_x_align_start_space = 0,
    absolute_x_align_end = false,
    absolute_x_align_end_space = 0,
    size_x_fit,
    size_x = '100px',
    size_x_max,
    size_x_min,
} = {}) => {
    console.log('1')

    let results = {
        ['.' + selector]: '',
    }

    let align_center_x = () => {
        return `
            margin-left: auto;
            margin-right: auto;
            left: 0;
            right: 0;
        `
    }

    let set_width = () => {
        if (size_x_fit)
            return `width: fit-content;`
        else
            return `width: ${lengthObj.length_css(lengthObj.length_json(String(size_x)))};`
    }

    if (absolute_x_align_center) {
        results['.' + selector] += `
            ${align_center_x()}
            ${set_width()}
        `

        // 如果width中存在 % 的话,加上CSS max-width,min-width
        if (/%/.test(String(size_x))) {
            results['.' + selector] += `
                max-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_max)))};
                min-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_min)))};
            `
        }

    } else {
        if (absolute_x_align_end) {

            results['.' + selector] += `
                right: ${lengthObj.length_css(lengthObj.length_json(String(absolute_x_align_end_space)))};
                `

            if (absolute_x_align_start) {
                results['.' + selector] += `
                left: ${lengthObj.length_css(lengthObj.length_json(String(absolute_x_align_start_space)))};
                `
            } else {
                results['.' + selector] += `
                    ${set_width()}
                `

                // 如果width中存在 % 的话,加上CSS max-width,min-width
                if (/%/.test(String(size_x))) {
                    results['.' + selector] += `
                        max-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_max)))};
                        min-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_min)))};
                    `
                }
            }
        } else {
            results['.' + selector] += `
                left: ${absolute_x_align_start_space};
                ${set_width()}
            `

            // 如果width中存在 % 的话,加上CSS max-width,min-width
            if (/%/.test(String(size_x))) {
                results['.' + selector] += `
                    max-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_max)))};
                    min-width: ${lengthObj.length_css(lengthObj.length_json(String(size_x_min)))};
                `
            }
        }
    }
    return results
}

let position_absolute_y = ({
    selector,
    absolute_y_align_center = false,
    absolute_y_align_start = false,
    absolute_y_align_start_space = 0,
    absolute_y_align_end = false,
    absolute_y_align_end_space = 0,
    size_y_fit,
    size_y = '100px',
    size_y_max,
    size_y_min,
} = {}) => {
    console.log('2')
    let results = {
        ['.' + selector]: '',
    }

    let align_center_y = () => {
        return `
            margin-top: auto;
            margin-bottom: auto;
            top: 0;
            bottom: 0;
        `
    }

    let set_height = () => {
        if (size_y_fit)
            return `width: fit-content;`
        else
            return `width: ${lengthObj.length_css(lengthObj.length_json(String(size_y)))};`
    }
    console.log('3')
    if (absolute_y_align_center) {
        console.log('33')
        results['.' + selector] += `
            ${align_center_y()}
            height: ${lengthObj.length_css(lengthObj.length_json(String(size_y)))};
        `
        console.log('3312')
            // 如果height中存在 % 的话,加上CSS max-height,min-height
        if (/%/.test(String(size_y))) {
            console.log('333')
            results['.' + selector] += `
                max-height: ${lengthObj.length_css(lengthObj.length_json(String(size_y_max)))};
                min-height: ${lengthObj.length_css(lengthObj.length_json(String(size_y_min)))};
            `
        }
    } else {
        console.log('4')
        if (absolute_y_align_end) {
            results['.' + selector] += `
                    bottom: ${lengthObj.length_css(lengthObj.length_json(String(absolute_y_align_end_space)))};`

            if (absolute_y_align_start) {
                results['.' + selector] += `top: ${lengthObj.length_css(lengthObj.length_json(String(absolute_y_align_start_space)))};`
            } else {
                results['.' + selector] += set_height()
            }
        } else {
            console.log('5')
            results['.' + selector] += `
                    top: ${
                        lengthObj.length_css(lengthObj.length_json(String(absolute_y_align_start_space)))
                    };
                    ${set_height()}
                    `

            // 如果height中存在 % 的话,加上CSS max-height,min-height
            if (/%/.test(String(size_y))) {
                results['.' + selector] += `
                max-height: ${
                    lengthObj.length_css(lengthObj.length_json(String(size_y_max)))
                };
                min-height: ${
                    lengthObj.length_css(lengthObj.length_json(String(size_y_min)))
                };
                `
            }
        }
    }
    console.log('6')
    return results
}
let position_relative = ({
    selector,
    relative_grow = 0,
    relative_shrink = 0,
    relative_align = 'unset'
} = {}) => {
    console.log('3')
    let results = {
        ['.' + selector]: '',
    }
    console.log('31')
    let fg = `
            flex-grow: ${
                lengthObj.length_css(lengthObj.length_json(String(relative_grow)))
            };`
    let fs = `
            flex-shrink: ${
                lengthObj.length_css(lengthObj.length_json(String(relative_shrink)))
            };`
    console.log('32')
    switch (relative_align) {
        case 'start':
            results['.' + selector] += `
                    ${fg}
                    ${fs}
                    align-self: flex-start;
                    `
            break
        case 'center':
            results['.' + selector] += `
                    ${fg}
                    ${fs}
                    align-self: center;
                    `
            break
        case 'end':
            results['.' + selector] += `
                    ${fg}
                    ${fs}
                    align-self: flex-end;
                    `
            break
        case 'baseline':
            results['.' + selector] += `
                    ${fg}
                    ${fs}
                    align-self: baseline;
                    `
            break
        case 'stretch':
            results['.' + selector] += `
                    ${fg}
                    ${fs}
                    align-self: stretch;
                    `
            break
    }
    console.log('box_position.js--> relative func-->', results)
    return results
}

export default {
    set_position_rule: set_position_rule,
    position_absolute_x: position_absolute_x,
    position_absolute_y: position_absolute_y,
    position_relative: position_relative,
}


